import React from 'react'
import { Text } from '@blockstack/ui'
import { Session } from '../auth'

const Auth = () => {
  if (!Session.isUserSignedIn()) {
    return null
  }

  return (
      <Text
        fontWeight="300"
        display="inline-block"
        ml={5}
        color="ink.400"
        cursor="pointer"
        onClick={() => {
          Session.signUserOut()
          window.location = '/'
        }}
      >
        Sign out
      </Text>
  )
}

export const Header = () => {
  return (
      <Auth />
  )
}
